	data(){
		return{
			statusBarHeight:''
		}
	},
	methods:{
		set(){
			uni.navigateTo({
				url:'../../component/set/Set'
			})
		},
		dd(){
			uni.navigateTo({
				url:'../../component/MyOrder/Subject/Subject'
			})
		}
	},
	onLoad() {
